@import './utils/vars';
@import './utils/mixins';

.button {
  width: 102px;

  @include elementSize(102px, 48px);

  @include fontParams(12px, 15px);
  color: #fff;
  background-color: #39bebf;
  text-transform: uppercase;

  border: none;

  transition: background-color $transitionDuration;

  &:hover {
    background-color: $c-green;
  }
}